import { combineReducers, createStore } from "redux";
import {todoListReducer} from "../reducers/todoListReducer";
import {tasksReducer} from "../reducers/tasksReducer";
import {loadState, saveState} from "../../common/utils/localstorage-utils";




const reducers = combineReducers({
    todoList: todoListReducer,
    tasks: tasksReducer,
})


//previous variant
// let preloadedState;
// const persistedTodoString = localStorage.getItem('app-state')
// if(persistedTodoString){
//     preloadedState = JSON.parse(persistedTodoString)
// }

export let store = createStore(reducers, loadState());
store.subscribe(()=>{
    saveState(store.getState())
})
//previous variant
// store.subscribe(()=>{
//     localStorage.setItem('app-state', JSON.stringify(store.getState()))
//     localStorage.setItem('value', JSON.stringify(store.getState().counter.value))
// })
export type StoreType = typeof store;
export type AppStateType = ReturnType<typeof reducers>;
//@ts-ignore
window.store = store